@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;300;400;600;700&display=swap);

.bg-dark,
.navbar-dark {
  background-color: #282C34 !important;
}

.navbar-nav .nav-link {
  color: rgb(255, 255, 255) !important;
  font-size: 1.3rem !important;
  font-weight: 300;
  transition: all .35s ease-in-out;
}

.navbar-nav .nav-link:hover {
  color: var(--blue-black)!important;
  border-bottom: 2px solid var(--blue-black);
}

.navbar-brand {
  color: #61dafb !important;
}

.navbar-brand-2 {
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  /* margin-right: 1rem; */
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
  color: #61dafb;
  /* display: none; */
}

.navbar-nav-2 {
  margin-left: 0px;
  padding-left: 0px;
}

.navbar-nav-2 a {
  color: #fff;
  padding-right: 0px
}

.navbar-nav-2 a:hover {
  color: #61dafb;
}

.jumbotron {
  background-color: #282C34 !important;
  border-radius: 0px !important;
  color: #fff !important;
  /* padding: 90px 20px; */
  /* margin-top: 5vh; url("https://www.rush.edu/sites/default/files/styles/2800x1152/public/2020-10/rcmc-homepage-hero-hires.jpg?itok=OOAo6fKN")*/
  background-image: linear-gradient(rgba(0, 0, 0, 0.45), 
  rgba(0, 0, 0, 0.45)),url(/react-hostpital-webiste/static/media/hero-img.afd05a2f.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 670px;
}

.jumbotron h1 {
  color: var(--white-text);
  font-size: 6vh;
  font-weight: 700;
  line-height: 69px;
}

.jumbotron p {
  font-size: 3vh;
  font-weight: 200;
  line-height: 35px;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.btn-primary.one{
  background-color: var(--blue-black) !important;
  color: var(--white-text) !important;
  border-radius: 0px !important;
  border: 0px !important;
  padding:15px !important;
  margin:0 2rem 0 0;
  font-weight: 300;
}

.btn-primary.two{
    background-color: var(--primary) !important;
    color: var(--black-text) !important;
    border-radius: 0px !important;
    border: 0px !important;
    padding:15px !important;
    
  }

.btn-primary.one:hover {
  background-color: var(--hover-2) !important;
  color: var(--white-text)!important;
}



.alert{
border-radius: 0px !important;
}

.alert a{
    color:#004085 !important; 
}

.dropdown-menu .dropdown-item{
    text-transform: capitalize !important;
    transition: all 20ms;
}

.dropdown-menu .dropdown-item:hover{
    color:white !important;
    background-color: var(--blue-black);
}
.btn-primary{
    background-color: var(--primary) !important;
    color: var(--black-text) !important;
    border-radius: 0px !important;
    border: 0px !important;
    padding:15px !important;
    
  }

  .btn-primary:hover{
background-color: var(--hover) !important;
  }
:root {
  --background: #DDE0E5;
  --primary: #F2CD00;
  --primary-border: #a2e2ff;

  --text: #424242;
  --love-text: #d12026;
  --muted-text: #ccc;
  --context-text: #6597b2;
  --white-text: #fff;
  --black-text: #000;
  --blue-black: #02123a;
  --button: #F2CD00;
  --hover:#F2AA00;
  --hover-2:#010a22;

  --card-background: #fff;
  --card-hover: rgba(220, 233, 255, 0.48);
  --table-separator: #dee2e6;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}


html{
  scroll-behavior:smooth;
}

body {
  font-family: 'Work Sans', sans-serif;
  font-size: 14px;
  /* background-color: var(--background); */
  color: #424242;
  color: var(--text);
}



.top-nav{
    background-color: #02123a;
    background-color: var(--blue-black);
    color:#fff;
    color:var(--white-text);
    padding: 0px 0px;
    font-size: .75rem;
}

.top-nav .btn{
    background-color: #F2CD00;
    background-color: var(--button);
    border:none;
    color:#000;
    color:var(--black-text);
    border-radius: 0px;
    padding: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: .9rem;
}

.top-nav .btn:hover{
background-color: #F2AA00;
background-color: var(--hover);
}

.main-nav{
    margin: 15px 15px !important;
    padding-left:0px !important;
    /* border:1px solid red */
}
.main-nav .nav-item a{
color:#000 !important;
color:var(--black-text) !important;
}
nav.navbar{
    margin-top:45px;
}
.hide{
    display: none;
}

.font-weight {
  font-weight: 200;
}

.f-1 {
  font-size: .9rem !important;
}


